<template>
  <div>
    <layout-box one="商品管理" two="商品审核">
      <el-card shadow="never" class="mb-1">
        <div>
          <i class="el-icon-search"></i>
          <span>筛选搜索</span>
          <el-button
            style="float: right"
            type="primary"
            size="small"
            @click="handleSearchList()"
          >
            查询结果
          </el-button>
          <el-button style="float:right;margin-right: 15px" size="small" @click="handleResetSearch">重置</el-button>

        </div>
        <div class="mt-1">
          <el-form :inline="true" size="small" label-width="140px" :model="queryInfo">
            <el-form-item label="审核状态：">
              <el-select v-model="queryInfo.approvalStatus">
                <el-option label="已审批" value="1"></el-option>
                <el-option label="未审批" value="0"></el-option>
                <el-option label="驳回" value="2"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="上架状态：">
              <el-select v-model="queryInfo.state" placeholder="请选择">
                <el-option label="上架" value="1"></el-option>
                <el-option label="已下架" value="0"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="商品名称：">
              <el-input v-model="queryInfo.name" placeholder="输入商品名称" clearable @clear="fetch"></el-input>
            </el-form-item>
          </el-form>
        </div>
      </el-card>
      <!-- 商品列表 -->
      <el-table
        v-loading="loading"
        class="mt-2"
        border
        :data="goodslist"
        style="width: 100%"
        max-height="500"
      >
        <el-table-column type="selection" width="80" align="center" />
        <el-table-column
          prop="name"
          label="商品图片"
          width="125"
          align="center"
        >
          <template slot-scope="scope">
            <el-image
              style="width: 100px; height: 100px"
              :src="scope.row.imageUrl"
            />
          </template>
        </el-table-column>
        <el-table-column label="商品视频" align="center" width="170">
          <template slot-scope="scope">
            <video :src="scope.row.videoUrl" preload="auto" controls width="150" height="130" />
          </template>
        </el-table-column>
        <el-table-column label="商品名称" align="center" show-overflow-tooltip>
          <template slot-scope="scope">
            <p>{{ scope.row.name }}</p>
          </template>
        </el-table-column>
        <el-table-column label="分类" align="center">
          <template slot-scope="scope">
            {{ scope.row.categoryName }}
          </template>
        </el-table-column>
        <el-table-column label="审批时间" align="center">
          <template slot-scope="scope">
            {{ scope.row.approvalTime }}
          </template>
        </el-table-column>
        <el-table-column align="center" label="价格/货号" width="150">
          <template slot-scope="scope">
            <p>价格：￥{{ scope.row.originalPrice }}</p>
            <p>货号: {{ scope.row.code }}</p>
          </template>
        </el-table-column>

        <el-table-column align="center" label="销量">
          <template slot-scope="scope">
            {{ scope.row.sales }}
          </template>
        </el-table-column>
        <el-table-column prop="address" align="center" label="库存">
          <template slot-scope="scope">
            {{ scope.row.totalInventory }}
          </template>
        </el-table-column>

        <el-table-column align="center" label="状态">
          <template slot-scope="scope">
            <span v-if="scope.row.state === 1" style="color: #40ccae">上架</span><span
              v-else-if="scope.row.state === 0"
              style="color: #e39025"
            >下架中</span>
          </template>
        </el-table-column>
        <el-table-column label="审核状态" align="center">
          <template slot-scope="scope">
            <span
              v-if="scope.row.approvalStatus === 0"
              style="color: #666666"
            >未审批</span>
            <span
              v-if="scope.row.approvalStatus === 1"
              style="color: #40ccae"
            >已审批</span>
            <span
              v-if="scope.row.approvalStatus === 2"
              style="color: #e02929"
            >驳回</span>
          </template>
        </el-table-column>
        <el-table-column prop="address" align="center" label="操作" width="179">
          <template slot-scope="scope">
            <el-button v-if="scope.row.approvalStatus === 0" @click="handleShowVerifyDetail(scope.row.id)">审核</el-button>
            <el-button type="danger" @click="removeUserById(scope.row.id)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 审批弹框 -->
      <el-dialog
        title="审核状态"
        :visible.sync="examine"
        width="30%"
      >
        <el-form ref="for" v-model="approve" label-width="80px">
          <el-form-item label="审核描述">
            <el-input v-model="approve.approveReason" type="textarea" :rows="2" placeholder="请输入内容" />
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="onSubmit">通过</el-button>
            <el-button @click="reject">驳回</el-button>
          </el-form-item>
        </el-form>
      </el-dialog>
      <el-pagination
        class="mt-1"
        :current-page="queryInfo.pageNo"
        :page-sizes="[2, 5, 10, 20]"
        :page-size="queryInfo.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      />
    </layout-box>
  </div>
</template>

<script>
const defaultListQuery = {
  pageNo: 1,
  pageSize: 10,
  name: null,
  code: null,
  state: null,
  approvalStatus: null
}
export default {
  data() {
    return {
      model: {},
      approve: {
        approveReason: '',
        approveStatus: 0
      },
      examine: false,
      editSkuInfo: {
        dialogVisible: false,
        productId: null,
        productSn: '',
        productAttributeCategoryId: null,
        stockList: [],
        productAttr: [],
      },
      // 加载
      loading: true,
      // 商品分页
      query: '',
      queryInfo: Object.assign({}, defaultListQuery),
      // 商品列表
      goodslist: [],
      state: null,
      // 总数据条数
      total: 0,
      value: '',
      // 0-未审批 1-已审批 2-驳回
      queryInf: {
        approveStatus: 1,
        id: ''
      },
      queryInf1: {
        approveStatus: 2,
        id: ''
      },
    }
  },
  created() {
    this.fetch()
  },

  methods: {
    handleResetSearch() {
      this.queryInfo = Object.assign({}, defaultListQuery)
      this.fetch()
    },
    handleSearchEditSku() {
      console.log(1)
    },
    handleEditSkuConfirm() {
      console.log(1)
    },
    // 审核按钮
    handleShowVerifyDetail(id) {
      this.examine = true
      this.queryInf.id = id
      this.queryInf1.id = id
    },
    // 审核通过
    async onSubmit(id) {
      const res = await this.$http.post('/woho/product/head/approve', this.queryInf)
      if (res.data.code !== 200) {
        this.$message.error('审核通过失败')
      }
      this.$message.success('审核通过成功')
      this.examine = false
      this.fetch()
    },
    // 审核驳回
    async reject(id) {
      const res = await this.$http.post('/woho/product/head/approve', this.queryInf1)
      console.log(res)
      if (res.data.code !== 200) {
        this.$message.error('审核驳回失败')
      }
      this.$message.success('审核驳回成功')
      this.examine = false
      this.fetch()
    },
    //  商品列表
    fetch() {
      this.loading = true
      let data = {
        isCheck: 1,
        type: 0,
        ...this.queryInfo
      }
      this.$http.post('/woho/product/head/query', data).then(res => {
        this.loading = false
        this.goodslist = res.data.data.list
        console.log(this.goodslist)
        this.total = res.data.data.total
      })
    },
    handleSizeChange(pageSize) {
      this.queryInfo.pageSize = pageSize
      this.fetch()
    },
    handleCurrentChange(pageNo) {
      this.queryInfo.pageNo = pageNo
      this.fetch()
    },
    // 商品删除
    removeUserById(id) {
      this.$confirm(`是否确定要删除该商品 "是否继续"`, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$http.delete(`/woho/product/head/delete/` + id).then(res => {
          this.$message({
            type: 'success',
            message: '删除成功!'
          })
          this.fetch()
        })
      })
    },
    zydescription(data) {
      this._getOrderItem(data.id)
    },
    // 搜索
    handleSearchList() {
      this.queryInfo.pageNo = 1
      this.fetch()
    },
  }
}
</script>

<style lang="scss" scoped>
.el-link {
  display: inline-block;
  width: 25%;
}

.box-card {
  min-width: 1300px;
}
.btns {
  color: #fff;
}
</style>
